.contact-form {
    display: flex;
    flex-direction: column;
    color: white;
}

.contact-form-input-div {
    display: flex;
    flex-direction: column;
}

.contact-form-label {
    margin-bottom: .2rem;
    font-size: .8rem;
    text-transform: uppercase;
    letter-spacing: 3px;
}

.contact-form-textarea, .contact-form-input {
    padding: .2rem .5rem;
    font-family: inherit;
    font-size: 1.2rem;
    color: white;
    font-weight: 600;
    margin-bottom: 1rem;
    background-color: rgba(255, 255, 255, 0.07);
    border: none;
    border-bottom: 2px solid white;
}
.contact-form-textarea:focus, .contact-form-input:focus {
    outline: none;
}

.contact-form-textarea {
    min-height: 12rem;
    resize: vertical;
}

.form-submit-button {
    padding: 1rem;
    margin-top: 1rem;
    margin-left: auto;
    width: 100%;
    border: 2px solid #E85D04;
    background-color: #E85D04;
    color: white;
    letter-spacing: 5px;
    text-transform: uppercase;
    transition: all .2s ease-in-out;
}

.form-submit-button:hover {
    color: #E85D04;
    background-color: transparent;
}

@media(min-width: 529px) {
    .contact-form {
        padding: 0 2rem;
    }

    .form-submit-button {
        width: 12rem;
    }
}