.project-grid {
    display: grid;
    grid-template-columns: auto;
    gap: 1rem;
}

.project-card {
    margin-bottom: 1rem;
    position: relative;
}

.project-card:last-of-type {
    margin-bottom: 0;
}

.project-card-title {
    font-size: 1.4rem;
    letter-spacing: 3px;
    font-weight: 500;
}

.project-card-sub {
    font-size: .7rem;
    margin: .3rem 0 1rem 0;
}

.project-card-description {
    font-size: .9rem;
}

@media(min-width: 529px) {
    .project-grid {
        grid-template-columns: auto;
        gap: 2rem;
    }
    
    .project-card {
        margin-bottom: 3rem;
        position: relative;
        padding: 0 2rem;
    }
    
    .project-card-title {
        font-size: 2.5rem;
        letter-spacing: 3px;
        font-weight: 500;
    }
    
    .project-card-sub {
        font-size: 1rem;
    }

    .project-card-description {
        font-size: 1.2rem;
    }
}