.carousel-container {
    position: relative;
    overflow: hidden;
    display: none;
    margin: 2rem auto;
}

.carousel-slider {
    display: flex;
    min-width: 100%;
    height: 100%;
}

.carousel-slider img {
    width: 100%;
    height: 100%;
}

.carousel-button-overlay {
    position: absolute;
    top: 0;
    display: flex;
    align-items: center;
    height: 100%;
    width: 15%;
    cursor: pointer;
}

.carousel-button-holder {
    padding: 1rem;
    font-size: 1.5rem;
    color: white;
    transform: translateX(100%);
    transition: transform .3s ease-in-out;
}

#prev .carousel-button-holder {
    transform: translateX(-100%);
}

#prev {
    left: 0;
}

#next {
    right: 0;
    justify-content: flex-end;
}

#prev:hover .carousel-button-holder, #next:hover .carousel-button-holder {
    transform: translateX(0);
}

.carousel-dots {
    position: absolute;
    bottom: 5px;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.carousel-dot {
    height: 10px;
    width: 10px;
    transform: scale(.9);
    border-radius: 50%;
    background-color: rgb(0, 0, 0, .3);
    margin: 0 .15rem;
    cursor: pointer;
    transition: all .3s ease-in-out;
}

.carousel-dot:hover, .current {
    transform: scale(1);
    background-color: white;
}

@media(min-width: 751px){
    .carousel-container {
        display: block;
        width: 640px;
        height: 360px;
    }
}

@media(min-width: 1100px) {
    .carousel-container {
        display: block;
        width: 800px;
        height: 450px;
    }
}

@media(min-width: 1441px) {
    .carousel-container {
        display: block;
        width: 960px;
        height: 540px;
    }
}

@media(min-width: 1800px) {
    .carousel-container {
        display: block;
        width: 1264px;
        height: 711px;
    }
}